import React, { useEffect } from 'react'
import AOS from "aos";
import "aos/dist/aos.css";
export const Card = (props) => {
  useEffect(() => {
    AOS.init();
    AOS.refresh();
  }, []);
  return (
    <div data-aos="flip-up" className='grid rounded-xl font-mono lg:h-[200px] sm:h-[140px] w-[200px] bg-[#252525]'>
        <div className='flex justify-center'>
        <img className='lg:h-[150px] sm:h-[100px] md:h-[120px] rounded-xl' src={props.icon } alt="/"></img>
        </div>

     <h2 className='text-[20px] text-[#ffd6ff]'> {props.title}</h2>
    </div>
  )
}
