import React from 'react'
import { Card } from "../UI/Card";
import C from '../assets/C.jpg'
import A from '../assets/A.png'
import R from '../assets/R.png'
import S from '../assets/S.png'

export const Services = () => {

  return (
    <> <h2 className="text-[50px] mt-36 text-[#55286f] mb-10">Services</h2>
    <div className='flex justify-center'>
        
    <div  className="mt-10 grid    lg:grid-cols-4
    gap-6 lg:gap-36  ">
      <Card title="Corporate Training " icon={C}/>
      <Card title="Accounting " icon={A}/>
      <Card title=" Social Media Marketing" icon={S}/>
      <Card title="Resume Writing " icon={R}/>
    </div>
    </div>
    </>
  )
}
