import React from 'react';
import AliceCarousel from 'react-alice-carousel';
import 'react-alice-carousel/lib/alice-carousel.css';
import pp1 from "../assets/pp1.jpg"
import pp2 from "../assets/pp2.jpg"
import pp3 from "../assets/pp3.jpg"
const handleDragStart = (e) => e.preventDefault();

const items = [
  <img src={pp2} className="lg:ml-24" alt="/" onDragStart={handleDragStart} role="presentation" />,
  <img src={pp1} className="lg:ml-24" alt="/"  onDragStart={handleDragStart} role="presentation" />,
  <img src={pp3} className="lg:ml-24"  alt="/" onDragStart={handleDragStart} role="presentation" />,
];

const Gallery = () => {
  return (
    <div className='mt-44 '>
    
          <AliceCarousel className="h-[400px]" mouseTracking items={items} />
    </div>
  
  );
}

export default Gallery; 